y-desctop-border;\n      &:active{\n        background-color: transparent;\n        color: $btn-secondary-desktop-text;\n        border: $btn-secondary-desctop-border;\n      }\n    }\n  }",".tab-label{\n    display: inline-block;\n    padding: 8px 12px;\n  \n    border-radius: $frame-rad-12;\n    border: $tab-border;\n    outline: none;\n  \n    background-color: $tab-bg;\n  \n    transition: $transition;\n\n    cursor: pointer;\n  }\n  \n  .tab-checkbox{\n    display: none;\n  }\n  .tab-checkbox:checked + .tab-label {\n    background-color: $tab-active-bg;\n    color: $tab-active-text;\n    border: $tab-active-border;\n  }",".pagination-item{\n    display: flex;\n    align-items: center;\n    justify-content: center;\n  \n    // font-size: clamp(18px, 0.5rem + 1.5vw, 20px);\n    font-size: 20px;\n    font-weight: 400;\n    // line-height: clamp(24px, 0.8rem + 1.8vw, 28px);\n    line-height: 1.4;\n  \n    color: $pagination-text;\n    background-color: $pagination-bg;\n    border-radius: $frame-rad-40;\n  \n    // width: clamp(48px, 1.5rem + 4vw, 60px);\n    // height: clamp(48px, 1.5rem + 4vw, 60px);\n    width: 60px;\n    height: 60px;\n  \n    cursor: pointer;\n  \n    &.active{\n      color: $pagination-active-text;\n      background-color: $pagination-active-bg;\n    }\n  \n  }",".pref-item{  \n    position: relative;\n    padding-left: 12px;\n  \n    @include tablet{\n      padding-left: 0;\n    }\n  \n  }\n  \n  .pref-item-svg-container{\n    display: flex;\n    align-items: center;\n    \n    width: clamp(100px, 4.3rem + 5.5vw, 150px);\n  \n  \n    position: relative;\n    z-index: 1;\n\n    @include tablet {\n      justify-content: center;\n    }\n    \n    &::before{\n      content: '';\n  \n      width: clamp(60px, 2rem + 5vw, 86px);\n      height: clamp(60px, 2rem + 5vw, 86px);\n      background-color: $pref-card-circle;\n      border-radius: 50%;\n  \n      position: absolute;\n      top: 50%;\n      right: -50%;\n      transform: translate(-50%,-50%);\n      z-index: -1;\n    }\n  \n  }\n  .pref-item-svg{  \n    // width: clamp(100px, 4rem + 7vw, 150px);\n    // height: clamp(100px, 4rem + 7vw, 150px);\n    width: 60px;\n    height: 60px;\n  \n    padding: clamp(19px, 0.4rem + 3.3vw, 43px);\n  \n    background-color: $main-bg;\n    border:$pref-card-border;\n    border-radius: $frame-rad-12;\n  \n    box-sizing: content-box;\n  }\n  .pref-item-title{\n    margin-top: clamp(16px, 0.6rem + 1.1vw, 24px);\n    margin-bottom: 12px;\n  }\n  .pref-item-descr{\n  \n  }",".about-us-item{\n    padding: 24px;\n    width: 100%;\n  \n    text-align: center;\n    \n    border: $about-us-border;\n    border-radius: clamp($frame-rad-16,2vw,$frame-rad-20);\n  \n    @include tablet{\n      max-width: 298px;\n    }\n  \n    &-svg{\n      width: clamp(40px, 1.2rem + 5vw, 60px);\n      height: clamp(40px, 1.2rem + 5vw, 60px);\n  \n      &.since{\n        width: clamp(72px, 2.5rem + 7vw, 104px);\n        height: clamp(40px, 1.5rem + 3vw, 60px);\n      }\n    }\n    \n  \n    &-title{\n      font-family: $russo;\n  \n      font-size: clamp(24px, 0.5rem + 3.5vw, 40px);\n      line-height: 1.4;\n      font-weight: 400;\n      color: $about-us-subheader-text;\n  \n      margin-top: 16px;\n      margin-bottom: 12px;\n    }\n  }",".label-input-with-icon{\n    position: relative;\n  \n    display: flex;\n    flex-direction: column;\n  \n    width: 100%;\n    max-width: 524px;\n    \n    .label-icon{\n      position: absolute;\n      top: 56px;\n      left: 24px;\n      fill: #3C4942;\n      stroke: #3C4942;\n      z-index: 1;\n    }\n  \n    .input-with-icon{\n  \n      margin-top: 12px;\n  \n      outline: none;\n      border: none;\n      border-radius: clamp($frame-rad-16,2vw,$frame-rad-20);\n      \n      color: $input-select-text;\n  \n      padding-top: 16px;\n      padding-bottom: 16px;\n      padding-left: 60px;\n      padding-right: 60px;\n  \n      &::placeholder{\n        color:$input-placeholder-text\n      }\n    }\n  \n}\n\n.label-input-with-icon.error .input-with-icon::placeholder{\n    color:$error-text;\n}\n.label-input-with-icon.error .error-icon{\n  opacity: 1;\n}\n.label-input-with-icon.error .error-message{\n  opacity: 1;\n}\n\n\n  \n.label-textarea{\n\n  display: flex;\n  flex-direction: column;\n\n  width: 100%;\n  max-width: 524px;\n\n  .textarea-field{\n    margin-top: 12px;\n\n    outline: none;\n    border: none;\n    border-radius: clamp($frame-rad-16,2vw,$frame-rad-20);\n    \n    color: $input-select-text;\n\n    padding: 16px 24px;\n\n    position: relative;\n\n    resize: none;\n\n    &::placeholder{\n      color:$input-placeholder-text\n    }\n  }\n  \n}","header{\n    position: relative;\n    z-index: 11;\n    margin: 0 auto;\n}\n\n\n.header-backround{\n    background-color: $card-bg;\n    border-bottom-left-radius: clamp($frame-rad-16,2vw,$frame-rad-24);\n    border-bottom-right-radius: clamp($frame-rad-16,2vw,$frame-rad-24);\n\n    padding-top: 16px;\n    padding-bottom: 16px;\n\n    @include tablet{\n        padding-top: clamp(12px, 2.3vw, 32px);\n        padding-bottom: 0;\n        background-color: transparent;\n        border-radius: 0;\n    }\n}\n//   clamp(min, current, max)\n//   window 1440px / max font-size = 44px\n//   1vw = 14.4px\n//   44px / 14.4px = 3.05vw\n\n\n.header-navigation{\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n\n    //\n    position: relative;\n    z-index: 11;\n\n    @include tablet{\n    padding-top: 16px;\n    padding-bottom: 16px;\n      padding-left: clamp(12px, -0.4rem + 4vw, 56px);\n      padding-right: clamp(12px, -0.4rem + 4vw, 56px);\n    }\n\n\n}\n\n.nav-list{\n    display: none;\n\n    @include tablet{\n        display: flex;\n        gap: clamp(12px, -3.877rem + 9.64vw, 44px);\n    }\n   \n}\n\n\n.header-link-phone{\n    display: none;\n\n    @include tablet{\n        display: inline-block;\n    }\n}\n\n.burger-button{\n    position: relative;\n    width: clamp(32px, 0.37rem + 6.52vw, 56px);\n    height: clamp(32px, 0.37rem + 6.52vw, 56px);\n\n    padding: 0;\n    background-color: transparent;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n\n    color: $white-text;\n\n    transition: $transition;\n\n    outline: none;\n    border: none;\n\n    cursor: pointer;\n\n    &:hover,&:focus{\n        color: $hover-accent-text\n    }\n    &:active{\n        color: $active-accent-text;\n    }\n    \n    &-icon{\n        position: absolute;\n        top: 0;\n        left: 0;\n\n        width: clamp(32px, 0.37rem + 6.52vw, 56px);\n        height: clamp(32px, 0.37rem + 6.52vw, 56px);\n\n        stroke: currentColor;\n\n        transition: $transition;\n\n    }\n\n    @include tablet{\n        display: none;\n    }\n\n\n\n}\n\n.burger-button-icon.btn-hide{\n    opacity: 0;\n}\n\n\n\n\n\n\n\n\n\n\n\n\n// .container.green-background{\n//     // padding-left: 0;\n//     // padding-right: 0;\n\n//     @include tablet{\n\n\n//     //   padding-left: clamp(12px, -0.4rem + 4vw, 56px);\n//     //   padding-right: clamp(12px, -0.4rem + 4vw, 56px);\n//       background-color: $card-bg;\n//       border-radius: clamp($frame-rad-16,2vw,$frame-rad-24);\n//     }\n// }\n",".upper-footer{\n    display: flex;\n    flex-wrap: wrap;\n    \n    @include tablet{\n        flex-wrap: nowrap;\n    }\n}\n\n.footer-map{\n    width: 100%;\n    height: clamp(320px, -0.436rem + 81.74vw, 620px);\n    \n    @include tablet{\n        max-width: 672px;\n        height: inherit;\n    }\n}\n\n.footer-map-img{\n    width: 100%;\n}\n\n.contacts{\n    display: flex;\n    flex-direction: column;\n    width: 100%;\n\n    @include tablet{\n        max-width: 672px;\n    }\n\n}\n\n.footer-contacts-item{\n    display: flex;\n    align-items: center;\n    gap: 16px;\n    color: $white-text;\n}\n\n.footer-contacts-item-link{\n    font-weight: 500;\n\n    display: flex;\n    align-items: center;\n    gap: 16px;\n}\n\n.footer-contacts-item-text-wrapper{\n    display: flex;\n    flex-direction: column;\n    gap: 4px;\n}\n\n.footer-contacts-item-text{\n    color: $main-text;\n}\n\n\n.footer-social-list{\n    display: flex;\n    gap: 24px;\n    color: $white-text;\n}\n\n\n.footer-list{\n    display: flex;\n    flex-direction: column;\n}\n.footer-item{\n    width: max-content;\n}\n.footer-link{\n    color: $white-text;\n}\n\n\n.footer-copyright{\n    text-align: center;\n\n    margin-bottom: 40px;\n    @include tablet{\n        margin-bottom: 32px;\n    }\n}",".burger-modal-bgd{\n    width: 100%;\n    height: 100%;\n    position: fixed;\n    top: 0;\n    left: 0;\n    background: $modal-bgd;\n\n    opacity: 0;\n    pointer-events: none;\n\n    transition: $transition-burger;\n    \n\n    filter:blur(5px);\n\n    z-index: 10;\n}\n\n.burger-modal-active {\n    opacity: 1;\n    pointer-events: all;\n}\n\n.burger-list{\n    padding-left:  clamp(18px, 0.344rem + 2.84vw, 32px);\n    padding-right: clamp(18px, 0.344rem + 2.84vw, 32px);\n\n    position: absolute;\n    left: 0;\n\n    // padding: 0 6px;\n    display: flex;\n    flex-direction: column;\n    gap: 0px;\n\n    background-color: $card-bg;\n    width: 100%;\n\n    border-bottom-left-radius: clamp($frame-rad-16,2vw,$frame-rad-24);\n    border-bottom-right-radius: clamp($frame-rad-16,2vw,$frame-rad-24);\n\n    transition: $transition-burger;\n\n    overflow: hidden;\n    pointer-events: none;\n\n    @include tablet{\n        display: none;\n    }\n\n    &.burger-show{\n        padding-top: 32px;\n        padding-bottom: 32px;\n    \n        pointer-events: all;\n\n        gap: 24px;\n    }\n\n}\n\n.burger-item{\n    width: 40%;\n    opacity: 0;\n    transition: $transition-burger;\n\n    & a{\n        transition: $transition-burger;\n        max-height: 0px;\n    }\n\n}\n\n// .burger-item:nth-child(1){\n//     transition-delay: .500ms;\n// }\n// .burger-item:nth-child(2){\n//     transition-delay: .450ms;\n// }\n// .burger-item:nth-child(3){\n//     transition-delay: .400ms;\n// }\n// .burger-item:nth-child(4){\n//     transition-delay: .350ms;\n// }\n// .burger-item:nth-child(5){\n//     transition-delay: .300ms;\n// }\n\n.burger-item.item-show{\n    opacity: 1;\n    & a{\n        max-height: 40px;\n    }\n}",".modal-bgd{\n    display: flex;\n    justify-content: center;\n    align-items: center;\n\n    width: 100%;\n    height: 100%;\n    position: fixed;\n    top: 0;\n    left: 0;\n    background: $modal-bgd;\n\n    opacity: 0;\n    pointer-events: none;\n\n    transition: $transition;\n    \n\n    z-index: 11;\n}\n\n.pop-up.modal-active {\n    opacity: 1;\n    pointer-events: all;\n}\n\n\n.pop-up-form{\n    display: none;\n    opacity: 0;\n    // display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n\n    margin-left: 12px;\n    margin-right: 12px;\n\n    width: 100%;\n    max-width: 676px;\n\n    position: relative;\n\n    transition: $transition;\n    // transition-duration: 300ms;  //кльовая фича но пока рано ( \n    // transition-behavior: allow-discrete; //кльовая фича но пока рано ( \n\n    @media screen and (max-width: 389px){\n        gap: 30px;\n    }\n\n\n}\n.pop-up-form.show{\n    // display: flex;\n    opacity: 1;\n\n    // @starting-style{  //кльовая фича но пока рано ( \n    //     opacity: 0;  //кльовая фича но пока рано ( \n    // }  //кльовая фича но пока рано ( \n}\n\n.form-inputs-wrapper{\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    gap: 32px;\n\n    width: 100%;\n}\n\n.form-inputs-name-tel{\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    justify-content: center;\n\n    gap: 32px;\n\n    width: 100%;\n}\n\n\n.error-icon{\n    position: absolute;\n    right: 24px;\n    bottom: 14px;\n    fill: $error-text;\n    opacity: 0;\n\n    transition: $transition;\n}\n\n\n\n\n.pop-up-close{\n    position: absolute;\n    top: 24px;\n    right: 24px;\n\n    padding: 0;\n\n    background-color: transparent;\n    border: none;\n   